import React, { useState, useEffect, useRef } from "react";
import { RepleContentDiv } from "./RepleCSS";
import { useSelector } from "react-redux";
import axios from "axios";
import Avatar from "react-avatar";
import moment from "moment";
import Swal from "sweetalert2";
import { LightBtn } from "../UI/Button";

const RepleContent = (props) => {
  const ref = useRef();
  const user = useSelector((state) => state.user);
  const [modalFlag, setModalFlag] = useState(false);
  const [editFlag, setEditFlag] = useState(false);
  const [reple, setReple] = useState(props.reple.reple);
  useOnClickOutside(ref, () => setModalFlag(false));

  const SubmitHandler = (e) => {
    e.preventDefault();
    let body = {
      uid: user.uid,
      reple,
      postId: props.reple.postId,
      repleId: props.reple._id,
    };

    axios.post("/api/reple/edit", body).then((response) => {
      if (response.data.success) {
        return Swal.fire({
          icon: "success",
          text: "ÎåìÍ∏ÄÏùÑ ÏàòÏ†ïÌïòÏòÄÏäµÎãàÎã§.",
          button: "ÌôïÏù∏",
        }).then(() => {
          window.location.reload();
        });
      } else {
        Swal.fire({
          icon: "error",
          text: "ÎåìÍ∏Ä ÏàòÏ†ïÏóê Ïã§Ìå®ÌïòÏòÄÏäµÎãàÎã§.",
          button: "ÌôïÏù∏",
        }).then(() => {
          window.location.reload();
        });
      }
    });
  };

  const DeleteHandler = (e) => {
    e.preventDefault();

    Swal.fire({
      icon: "warning",
      text: "Ï†ïÎßêÎ°ú ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå?",
      showCancelButton: true,
      confirmButtonText: "ÌôïÏù∏",
      cancelButtonText: "Ï∑®ÏÜå",
      confirmButtonColor: "#50577a",
      cancelButtonColor: "#BEBCBA",
    }).then((result) => {
      if (result.isConfirmed) {
        let body = { repleId: props.reple._id, postId: props.reple.postId };

        axios
          .post("/api/reple/delete", body)
          .then((response) => {
            if (response.data.success) {
              Swal.fire({
                icon: "success",
                text: "ÎåìÍ∏ÄÏù¥ ÏÇ≠Ï†úÎêòÏóàÏäµÎãàÎã§.",
                button: "ÌôïÏù∏",
              }).then(() => {
                window.location.reload();
              });
            }
          })
          .catch((err) => {
            Swal.fire({
              icon: "error",
              text: "ÎåìÍ∏Ä ÏÇ≠Ï†úÏóê Ïã§Ìå®ÌïòÏòÄÏäµÎãàÎã§.",
              button: "ÌôïÏù∏",
            }).then(() => {
              window.location.reload();
            });
          });
      }
    });
  };

  const setTime = (a, b) => {
    if (a !== b) {
      return moment(b).format("YYYYÎÖÑ MMMM Do, HH:mm") + " (ÏàòÏ†ïÎê®)";
    } else {
      return moment(a).format("YYYYÎÖÑ MMMM Do, HH:mm");
    }
  };

  const handleQuitEdit = () => {
    setEditFlag(false);
    setReple(props.reple.reple);
  };

  return (
    <RepleContentDiv>
      <div className="content-style">
        {editFlag ? (
          <form className="edit-style">
            <input
              type="text"
              value={reple}
              onChange={(e) => setReple(e.currentTarget.value)}
            />
            <div className="button-style">
              <LightBtn onClick={handleQuitEdit}>Ï∑®ÏÜå</LightBtn>
              <LightBtn onClick={(e) => SubmitHandler(e)}>ÏàòÏ†ï ÏôÑÎ£å</LightBtn>
            </div>
          </form>
        ) : (
          <p className="reple-style">{props.reple.reple}</p>
        )}
        {props.reple.author.uid === user.uid && (
          <div className="modalControl">
            <span onClick={() => setModalFlag(true)}>üõ†</span>
            {modalFlag && (
              <div className="modalDiv" ref={ref}>
                <p
                  onClick={() => {
                    setEditFlag(true);
                    setModalFlag(false);
                  }}
                >
                  ÏàòÏ†ï
                </p>
                <p className="delete" onClick={(e) => DeleteHandler(e)}>
                  ÏÇ≠Ï†ú
                </p>
              </div>
            )}
          </div>
        )}
      </div>
      <div className="info-style">
        <Avatar size="30" round={true} src={props.reple.author.photoURL} />

        <p className="author">{props.reple.author.displayName}</p>
        <p className="time">
          {setTime(props.reple.createdAt, props.reple.updatedAt)}
        </p>
      </div>
    </RepleContentDiv>
  );
};

// // Î™®Îã¨ Î∞ñ ÌÅ¥Î¶≠Ïãú Î™®Îã¨Ï∞Ω Îã´ÌûàÎèÑÎ°ù ÌïòÎäî Hook
function useOnClickOutside(ref, handler) {
  useEffect(() => {
    const listener = (event) => {
      if (!ref.current || ref.current.contains(event.target)) {
        return;
      }
      handler(event);
    };
    document.addEventListener("mousedown", listener);
    document.addEventListener("touchstart", listener);
    return () => {
      document.removeEventListener("mousedown", listener);
      document.removeEventListener("touchstart", listener);
    };
  }, [ref, handler]);
}

export default RepleContent;
