import React, { useState } from "react";
import axios from "axios";
import { useSelector } from "react-redux";
import { RepleUploadDiv } from "./RepleCSS";
import Toast from "../UI/Toast/Toast";
import Swal from "sweetalert2";

const RepleUpload = (props) => {
  const [reple, setReple] = useState("");
  const user = useSelector((state) => state.user);

  const SubmitHandler = (e) => {
    e.preventDefault();

    if (!reple) {
      return Toast.fire({
        position: "bottom",
        icon: "warning",
        text: "ÎåìÍ∏Ä ÎÇ¥Ïö©ÏùÑ Ï±ÑÏõåÏ£ºÏÑ∏Ïöî.",
      });
    }

    let body = {
      reple,
      uid: user.uid,
      postId: props.postId,
    };

    axios.post("/api/reple/submit", body).then((response) => {
      if (response.data.success) {
        Swal.fire({
          icon: "success",
          text: "ÎåìÍ∏ÄÏùÑ Îì±Î°ùÌïòÏòÄÏäµÎãàÎã§.",
          button: "ÌôïÏù∏",
        }).then(() => {
          window.location.reload();
        });
      } else {
        Toast.fire({
          position: "bottom",
          icon: "error",
          text: "ÎåìÍ∏Ä ÏûëÏÑ±Ïóê Ïã§Ìå®ÌïòÏòÄÏäµÎãàÎã§.",
        });
      }
    });
  };

  return (
    <RepleUploadDiv>
      <form>
        <input
          type="text"
          value={reple}
          onChange={(e) => setReple(e.currentTarget.value)}
        />
        <button onClick={(e) => SubmitHandler(e)}>‚èé</button>
      </form>
    </RepleUploadDiv>
  );
};

export default RepleUpload;
